video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
}

.about-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  object-fit: contain;
}

.about-container > h1 {
  color: #fff;
  font-size: 6vw;
  padding-bottom: 0px;
}

.about-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}


/* goofy cards */
*, *::before, *::after {
  box-sizing:content-box; /* Too much math below to be concerned with standard CSS Box Model element dimensions calculations. Let's not have to account for padding/margins/borders on anything here. */
}

body {
  margin: 0;
  background-attachment: fixed;
  background-size: cover;
}

h1 {
  padding: 20px;
  color: #fff;
  text-align: center;
}

.carousel_wrapper {
  padding-top: 10vh;
  position: relative;
  width: 290px;
  margin: 100px auto 0 auto;
  perspective: 1300px;
}

.carousel {
  position: relative;
  text-align: center;
  color: white;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transform: rotateY(-360deg) translateZ(-412px); /* add reverse transformation from the slides */
  animation: swirl 40s steps(10000, end) infinite; /* run `swirl` animation (defined at end of CSS) infitely, with animation lasting 40 seconds, and 10,000 steps between the FROM and TO values in the animation for a smooth rotation */
}

.slide {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 300px;
  height: 300px;
  background-size: 300px;
  background-position: center;
  z-index: 200;
}

.slide img {
  width: 300px;
  height: 300px;
  display: block;
  object-fit:cover;
  filter: brightness(50%);
  
}

.image-info {
  font-family: 'Montserrat', sans-serif;
  width: 100%;
  position: absolute;
  top: 80%;
  left: 60%;
  transform: translate(-50%, -50%);
  z-index: 20;
  text-align: left;
}

.image-info p {
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  color: #fff;
  z-index: 100;
  color: white;
  font-size: 20px;
  text-shadow:
      0.07em 0 black,
      0 0.07em black,
      -0.07em 0 black,
      0 -0.07em black;
}

.image-info h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 30px;
  position: absolute;
  top: -170%;
  left: 40%;
  transform: translate(-50%, -50%);
  z-index: 20;
  text-align: center;
}

