.slide.one {
  transform: rotateY(0deg) translateZ(412px);
}
.slide.two {
  transform: rotateY(90deg) translateZ(412px);
}
.slide.three {
  transform: rotateY(180deg) translateZ(412px);
}
.slide.four {
  transform: rotateY(270deg) translateZ(412px);
}
