/*
CSS3 ANIMATION
-------------------
Simply rotates the carousel around the Y axis by using rotateY and starting at initial value, -360º, and going to final value 0º, then resetting.
*/

@keyframes swirl {   
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
} 

@media screen and (max-width: 450px) {
  .about-container > h1 {
    color: #fff;
    font-size: 8vw;
    padding-bottom: 0px;
  }
  .carousel_wrapper {
    padding-top: 10vh;
    position: relative;
    width: 290px;
    margin: 100px auto 0 auto;
    perspective: 1300px;
  }

  .carousel {
    position: relative;
    text-align: center;
    color: white;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transform: rotateY(-360deg) translateZ(-180px); /* add reverse transformation from the slides */
    animation: swirl 20s steps(10000, end) infinite; /* run `swirl` animation (defined at end of CSS) infitely, with animation lasting 40 seconds, and 10,000 steps between the FROM and TO values in the animation for a smooth rotation */
  }

  .slide.one {
    transform: rotateY(0deg) translateZ(180px);
  }
  .slide.two {
    transform: rotateY(90deg) translateZ(180px);
  }
  .slide.three {
    transform: rotateY(180deg) translateZ(180px);
  }
  .slide.four {
    transform: rotateY(270deg) translateZ(180px);
  }
}



